import React from 'react';
import { Routes, Route } from 'react-router-dom';

import CmsContent from './CmsContent';
import CmsPosts from './CmsPosts';
import CmsPost from './CmsPost';

const Content = () => {

    const isWaiting = () => {
        return (
            <div className="lazy-loader">
                Loading...
                {/* <img src={asset_loader} alt="Loading" /> */}
            </div>
        );
    };

    
    return (
        <div className="container-fluid p-4">
            <React.Suspense fallback={isWaiting()}>
                <Routes>
                    <Route path='/' element={<CmsContent />} />
                    <Route path='/posts/:content_id' element={<CmsPosts />} />
                    <Route path='/new-post/:content_id' element={<CmsPost />} />
                    <Route path='/view-post/:content_id/:post_id' element={<CmsPost />} />
                    <Route path='/edit-post/:content_id/:post_id' element={<CmsPost />} />
                </Routes>
            </React.Suspense>
        </div>
    );
};

export default Content;