import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { useTranslation } from 'react-i18next';
import WebContext from '../../../application/contexts/WebContext';

import AdminApis from '../APIs/AdminApis';

import DataNotFound from '../../../infrastructure/Common/DataNotFound';
// import Table from '@mui/material/Table';
// import TableBody from '@mui/material/TableBody';
// import TableCell from '@mui/material/TableCell';
// import TableContainer from '@mui/material/TableContainer';
// import TableHead from '@mui/material/TableHead';
// import TableRow from '@mui/material/TableRow';
// import Paper from '@mui/material/Paper';

// import ApiService from '../../_servicesAdmin/apiAdmin';
// import DeletePost from './../DeletePost';

import './content.scss';
// import MaterialTable from '../../Shared/MaterialTable';

const CmsContent = () => {
    const { t } = useTranslation();
    const webCtx = useContext(WebContext);

    const [isLoading, setIsLoading] = useState(true);
    const [filterText, setFilterText] = useState('');
    const [postsData, setPostsData] = useState([]);

    const [modal, setModal] = useState('');
    const [modalData, setModalData] = useState([]);

    useEffect(() => {
        getCmsContent();
    }, []);

    const getCmsContent = () => {
        AdminApis.cmsContent()
            .then(res => {
                setPostsData(res.data);
                // setTimeout(() => {
                    setIsLoading(false);
                // }, 2000);
            })
            .catch(err => {
                console.log(err);
                setIsLoading(false);
            })
    };

    const handleDeleteCategoryRes = (data) => {
        const temp = [...postsData];
        temp.splice(data.index, 1);
        setPostsData(temp);
        setModal('');
    };

    const handleModel = (name, item = {}, index = null) => {
        setModal(name);
        setModalData({ ...item, index });
    }

    return (

        <div className="admin-cms">
            <div className="d-block d-md-flex align-items-end justify-content-between mb-4">
                <div className="section-header mb-3 mb-md-0">
                    <div className="primary">{t('content')}</div>
                    <h5 className="title fw-medium mb-0">{t('cms_desc')}</h5>
                </div>

                <div className="d-flex mb-3 mb-md-0">
                    <div className="col-auto">
                        <input type="text" name="filter" id="filter" placeholder={t('filter')} className="nodis-field form-control" onChange={(e) => setFilterText(e.target.value)} />
                    </div>
                    <div className="col-auto">
                        <Link to="/admin-portal/cms/new-post/0">
                            <button className="btn nodis-btn outline ms-3 ">+ {t('add_new_post')}</button>
                        </Link>
                    </div>
                </div>
            </div>

            <div className="section-data">
                {/* <div className="nodis-table condensed mt-1">
                        <MaterialTable data={postsData} filterText={filterText} setFilterText={setFilterText} />
                    </div> */}

                <div className="min-h-250">
                    {isLoading ?
                        <div className="content-placeholder p-3">
                            <h5 className="placeholder-glow mb-4">
                                <span className="placeholder col-6"></span>
                            </h5>
                            <p className="card-text placeholder-glow">
                                <span className="placeholder col-7 mt-3"></span>
                                <span className="placeholder col-4 mt-3"></span>
                                <span className="placeholder col-6"></span>
                                <span className="placeholder col-6"></span>
                                <span className="placeholder col-6"></span>
                                <span className="placeholder col-3"></span>
                                <span className="placeholder col-6"></span>
                                <span className="placeholder col-2"></span>
                                <span className="placeholder col-7"></span>
                            </p>
                        </div>
                        :
                        <>
                            {postsData && postsData.length ?

                                <table className="nodis-table out-border-0 table condensed mb-0">
                                    <thead>
                                        <tr>
                                            <th className="index">#</th>
                                            <th>{t('title')}</th>
                                            <th className="d-none d-sm-table-cell">{t('label')}</th>
                                            <th className="d-none d-sm-table-cell">{t('status')}</th>
                                            <th>{t('actions')}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {postsData.length && postsData.map((item, index) => (
                                            <tr key={index}>
                                                <td className="index">{index + 1}</td>
                                                <td>
                                                    <div></div>
                                                    <div className="fw-bold title">{item['title_' + webCtx.appLang]}</div>
                                                </td>
                                                <td className="d-none d-sm-table-cell">
                                                    <div className="nodis-tag">{item?.label}</div>
                                                </td>
                                                <td className="d-none d-sm-table-cell">
                                                    <div className={'capitalize ' + (item?.status === 'publish' ? 'success' : 'warning')}>{item?.status}</div>
                                                </td>
                                                <td>
                                                    <Link to={`posts/${item.id}`}><button className="btn nodis-btn outline small px-3">{t('open')}</button></Link>
                                                    {/* <button className="btn nodis-btn danger small px-3 ms-2" onClick={() => handleModel('delete', item, index)}>Delete</button> */}

                                                    {/* <a href="https://www.azharmughal.com/" className="btn nodis-btn outline small px-3">{t('view')}</a> */}
                                                </td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>

                                : null}
                        </>
                    }
                    {!isLoading && !postsData.length && <DataNotFound />}
                </div>

            </div>
            {/* {modal &&
                <DeletePost setModal={setModal} modal={modal === 'delete'} data={modalData} onDeleteSuccess={handleDeleteCategoryRes} />
            } */}
        </div>
    )
}

export default CmsContent;
